    .SmsPart {
        width: 94%;
        /* height: 70px; */
        background-color: #1a1a1a;
        display: block;
        margin: 10px auto;
        padding: 2px;
        overflow: hidden;
        border-radius: 25px;
        -webkit-border-radius: 25px;
        -moz-border-radius: 25px;
        -ms-border-radius: 25px;
        -o-border-radius: 25px;
        /* border: 2pt solid; */
        z-index: 2;
        /* padding: 10px; */
        padding-right: 5px;
    }
    
    #part1-SmsPart {
        width: 100%;
        margin-top: 5px;
        /* justify-content: space-between; */
    }
    
    .icon-SmsPart {
        position: relative;
        width: 50px;
        height: 50px;
        /* background-color: bisque; */
        background-image: url(https://s24.picofile.com/file/8451952150/speech_bubble_1_.png);
        background-position: center;
        background-size: 50%;
        background-repeat: no-repeat;
        border-radius: 50%;
        border: 1pt solid #0afc96;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        float: right;
        /* top: 50%; */
    }
    
    .description-SmsPart {
        /* background-color: chartreuse; */
        width: 60%;
        text-align: right;
        float: right;
        margin-top: 14px;
        margin-right: 7px;
        /* margin-top: 50px; */
    }
    
    .description-SmsPart p:nth-child(1) {
        font-size: medium;
        font-weight: 400;
        color: white;
        margin-right: 5px;
    }
    
    .open-SmartPart {
        width: 30px;
        height: 30px;
        float: left;
        background-color: rgb(39, 37, 37);
        box-shadow: 2pt 2pt 10pt black;
        border: 1pt solid #0afc96;
        background-image: url('./public/icons/white/chevron.png');
        /* rotate: calc(-90deg); */
        /*transform: rotate(90);
        */
        background-position: center;
        background-size: 50%;
        background-repeat: no-repeat;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        /*border: 2pt solid #009b8b;
        */
        /* transform: rotate(0); */
        margin-top: 8px;
        margin-left: 5px;
        box-shadow: 2pt 2pt 2pt rgba(0, 0, 0, 0.596);
    }
    
    #part2-SmsPart {
        position: relative;
        width: 95%;
        /* height: 60px; */
        /* background-color: red; */
        margin: 70px auto;
    }
    
    #part2-SmsPart .input-SmsPart {
        width: 100%;
        /* height: 70px; */
        /* background-color: aqua; */
        margin: 10px auto;
    }
    
    #part2-SmsPart .input-SmsPart textarea {
        width: 98%;
        height: 120px;
        border-radius: 20px;
        -webkit-border-radius: 20px;
        -moz-border-radius: 20px;
        -ms-border-radius: 20px;
        -o-border-radius: 20px;
        border: none;
        background-color: rgb(39, 37, 37);
        box-shadow: 2pt 2pt 10pt black;
        text-align: right;
        color: white;
        padding-right: 5px;
        padding-top: 5px;
    }
    
    #part2-SmsPart .controllers-SmsPart {
        margin: 16px auto;
        width: 100%;
        /* background-color: green; */
        display: flex;
        justify-content: space-around;
    }
    
    #part2-SmsPart .controllers-SmsPart a {
        width: 40%;
        height: 35px;
        /* background-color: red; */
        display: flex;
        justify-content: center;
    }
    
    #part2-SmsPart .controllers-SmsPart a button {
        width: 70px;
        height: 30px;
        background-color: rgb(39, 37, 37);
        box-shadow: 2pt 2pt 10pt black;
        border: 1pt #0afc96 solid;
        border-radius: 15px;
        /* border: none; */
        font-size: small;
        font-weight: 400;
        -webkit-border-radius: 15px;
        -moz-border-radius: 15px;
        -ms-border-radius: 15px;
        -o-border-radius: 15px;
        box-shadow: 2pt 2pt 2pt rgba(0, 0, 0, 0.596);
        color: white;
    }
