.Charge {
    /* position: absolute; */
    width: 100vw;
    height: 100vh;
    /* background-color: #ffffff; */
    background-color: #009b8c7c;
    font-size: medium;
    font-weight: 400;
    background-image: url('https://s25.picofile.com/file/8451585292/tina_dawson_Kim9COAIEGc_unsplash_5922e7ed1d3da93bbae6.jpg');
    background-position: center;
    background-size: cover;
    bottom: 0px;
    overflow: hidden;
}

#part1-Charge {
    position: absolute;
    width: 92.5vw;
    /* max-width: 300px; */
    height: 80vw;
    /*height: 65px;
    */
    /*background-color: red;
    */
    margin: 40px auto;
    padding: 5px;
    overflow-y: scroll;
    overflow-x: hidden;
    background-color: #1a1a1a;
    box-shadow: 2pt 2pt 10pt black;
    border-radius: 15px;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;
    left: 50%;
    transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);
}

#part1-Charge .cach-Charge {
    width: 150px;
    height: 150px;
    background-color: rgba(255, 255, 255, 0.548);
    border: 2pt solid rgb(255, 255, 255);
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    padding: 20px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}

#part1-Charge .increase-Charge {
    position: absolute;
    width: 100%;
    /* background-color: green; */
    display: flex;
    justify-content: space-around;
    /* margin-top: 30px; */
    margin-bottom: 30px;
    /* background-color: red; */
    bottom: 0px;
}

#part1-Charge .tell-Charge {
    margin-top: 30px;
    font-family: 'Vazirmatn', sans-serif;
    font-size: large;
    /* text-decoration: underline; */
    letter-spacing: 1px;
    line-height: 45px;
    font-size: medium;
    font-weight: 400;
    padding: 5px;
    color: white;
    margin-bottom: 30px;
    text-align: center;
    /* background-color: red; */
}

#part1-Charge .increase-Charge input {
    width: 200px;
    height: 35px;
    border-radius: 60px;
    -webkit-border-radius: 60px;
    -moz-border-radius: 60px;
    -ms-border-radius: 60px;
    -o-border-radius: 60px;
    border: none;
    text-align: center;
    border: 2pt solid #009b8b;
    font-size: large;
    box-shadow: 2pt 2pt 2pt rgba(0, 0, 0, 0.596);
}

#part1-Charge .increase-Charge .submitPart-Charge button {
    width: 85px;
    height: 30px;
    background-color: rgb(39, 37, 37);
    box-shadow: 2pt 2pt 10pt black;
    border: none;
    border-radius: 25px;
    -webkit-border-radius: 25px;
    -moz-border-radius: 25px;
    -ms-border-radius: 25px;
    -o-border-radius: 25px;
    font-size: medium;
    /* font-weight: 500; */
    font-weight: 400;
    font-size: small;
    box-shadow: 2pt 2pt 2pt rgba(0, 0, 0, 0.596);
    color: white;
    border: 1pt solid #0afc96;
}