.ZoonInfo {
    width: 100vw;
    height: 100vh;
    background-color: #009b8c;
    font-size: large;
    /* font-family: 600; */
    background-image: url('https://s25.picofile.com/file/8451585292/tina_dawson_Kim9COAIEGc_unsplash_5922e7ed1d3da93bbae6.jpg');
    background-position: center;
    background-size: cover;
    overflow: hidden;
    color: white;
}

#part2-ZoonInfo {
    width: 100%;
    height: fit-content;
    /* border: 2pt solid #009b8c; */
    margin-top: 10px;
    overflow: hidden;
    border-radius: 15px;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;
    overflow: hidden;
}

#part2-ZoonInfo .items-ZoonInfo {
    width: 97%;
    height: 66vh;
    /* background-color: pink; */
    margin: 0 auto;
    margin-top: 1px;
    /* background-color: red; */
}

#part2-ZoonInfo .items-ZoonInfo .item-ZoonInfo {
    position: relative !important;
    background-color: #1a1a1a;
    width: 100%;
    height: 46px;
    display: flex;
    justify-content: space-between;
    border-radius: 15px;
    overflow: hidden;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;
    /* border: 2pt solid white; */
    margin-top: 8px;
    left: 50%;
    transform: translate(-50%);
    padding: 0px;
    -webkit-transform: translate(-50%);
    -moz-transform: translate(-50%);
    -ms-transform: translate(-50%);
    -o-transform: translate(-50%);
    padding-left: 5px;
}

#part2-ZoonInfo .items-ZoonInfo .item-ZoonInfo a,
#part2-ZoonInfo .items-ZoonInfo .item-ZoonInfo .number-ZoonInfo,
#part2-ZoonInfo .items-ZoonInfo .item-ZoonInfo .name-ZoonInfo,
#part2-ZoonInfo .items-ZoonInfo .item-ZoonInfo .operator-ZoonInfo {
    width: 30%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    /* background-color: red; */
    /* border: 2pt solid black; */
    font-size: small;
    font-weight: 400;
    /* color: black; */
    text-align: center;
}

#part2-ZoonInfo .items-ZoonInfo .item-ZoonInfo a .state-ZoonInfo {
    /* background-color: red; */
    display: flex;
    flex-direction: row;
    /* justify-content: space-between; */
    padding: 5px;
}

#part2-ZoonInfo .items-ZoonInfo .item-ZoonInfo .operator-ZoonInfo {
    width: 35px;
    height: 35px;
    overflow: hidden;
    background-color: rgb(39, 37, 37);
    box-shadow: 2pt 2pt 10pt black;
    border: 1pt solid #0afc96;
    position: relative;
    text-align: left;
    text-align: center;
    left: 5px;
    top: 50%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    /*border: 2pt solid #009b8c;
    */
    /*box-shadow: 2pt 1pt 1pt rgba(255, 255, 255, 0.459);
    */
    box-shadow: 2pt 2pt 2pt rgba(0, 0, 0, 0.596);
    margin-right: 4px;
    color: black;
    background-image: url(https://s25.picofile.com/file/8451959926/edit_1_.png);
    background-position: center;
    background-size: 50%;
    background-repeat: no-repeat;
}

#part2-ZoonInfo .items-ZoonInfo .item-ZoonInfo a .state-ZoonInfo div {
    position: relative;
    text-align: left;
    top: 50%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
}

#part2-ZoonInfo .items-ZoonInfo .item-ZoonInfo a .state-ZoonInfo div p {
    position: relative;
    top: 50%;
    /* background-color: green; */
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    text-align: center;
}


