#part2-ZoonInfo .items-ZoonInfo .item-ZoonInfo .cotroller-ZoonInfo {
    /* background-color: red; */
    display: flex;
}

#part2-ZoonInfo .items-ZoonInfo .item-ZoonInfo .cotroller-ZoonInfo a {
    position: relative;
    /*background-color: rgb(250, 250, 162);
    */
    width: 35px;
    height: 35px;
    overflow: hidden;
    background-color: rgb(39, 37, 37);
    box-shadow: 2pt 2pt 10pt black;
    border: 1pt solid #0afc96;
    background-image: url(https://s25.picofile.com/file/8451911434/bin.png);
    background-position: center;
    background-size: 50%;
    background-repeat: no-repeat;
    /*margin-top: 2px;
    */
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    /*border: 2pt solid #009b8c;
    */
    /*box-shadow:;
    2pt1pt1ptrgba(255,255,255,0.459)*/
    top: 50%;
    transform: translateY(-50%);
    box-shadow: 2pt 2pt 2pt rgba(0, 0, 0, 0.596);
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
}

.table-ZoonInfo .title-ZoonInfo {
    width: 100%;
    height: 6vh;
    background-color: #1a1a1a;
    display: flex;
    justify-content: space-between;
    border-bottom: 1pt solid #0afc96;
    font-size: medium;
    font-weight: 400;
    color: white;
}

.table-ZoonInfo .title-ZoonInfo div {
    width: 30%;
    height: 100%;
    /* background-color: red;*/
    /* border: 2pt solid; */
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.table-ZoonInfo .title-ZoonInfo div p {
    /* background-color: aqua; */
    margin: 0 auto;
}

