.add-ZoonInfo .addIcon-ZoonInfo {
    width: 47px;
    height: 47px;
    /* /*background-color: red; */
    margin: 0 auto;
    background-image: url('https://s24.picofile.com/file/8451586034/plus_1_.png');
    background-position: center;
    background-size: 55%;
    background-repeat: no-repeat;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
