.Contact {
    width: 95%;
    height: 60px;
    background-color: #1a1a1ae7;
    display: flex;
    /* border-bottom: 1pt solid #009b8b; */
    border-radius: 15px;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;
    padding-right: 5px;
    margin: 0 auto;
    margin-top: 7px;
    color: white;
}

#part1-Contact {
    /* background-color: brown; */
    /* width: 60px; */
    height: 60px;
    display: flex;
}

#part1-Contact .addIcon-Contact {
    position: static;
    width: 35px;
    height: 100%;
    background-size: 60%;
    background-position: center;
    background-repeat: no-repeat;
    /*top: 50%;
    */
    left: 10px;
    /*transform: translateY(-50%);
    */
    /* border: 2pt solid; */
    padding-left: 5px;
    /* top: 50%;
    transform: translate(0%, -50%);
    -webkit-transform: translate(0%, -50%);
    -moz-transform: translate(0%, -50%);
    -ms-transform: translate(0%, -50%);
    -o-transform: translate(0%, -50%);
    -webkit-transform: translate(-50%, -50%); */
}

#part1-Contact .addIcon-Contact button {
    position: relative;
    background-color: rgb(39, 37, 37);
    box-shadow: 2pt 2pt 10pt black;
    border: 1pt solid #0afc96;
    width: 35px;
    height: 35px;
    /*border: 2pt solid #009b8c;
    */
    box-shadow: 2pt 2pt 2pt rgba(0, 0, 0, 0.596);
    /* border: 2pt solid #009b8c; */
    /* border: none; */
    font-size: small;
    font-weight: 400;
    border-radius: 5;
    -webkit-border-radius: 50%;
    -moz-border-radius: 5;
    -ms-border-radius: 5;
    -o-border-radius: 5;
    top: 50%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
}

#delet-Contact {
    background-image: url(https://s25.picofile.com/file/8451911434/bin.png);
    background-position: center;
    background-size: 50%;
    background-repeat: no-repeat;
}

#edit-Contact {
    background-image: url(https://s25.picofile.com/file/8451959926/edit_1_.png);
    background-position: center;
    background-size: 50%;
    background-repeat: no-repeat;
}

#part1-Contact div:first button {}

#part2-Contact {
    /* background-color: cadetblue; */
    height: 60px;
    display: flex;
    width: 90%;
    flex-direction: row-reverse;
}

.icon-Contact {
    position: relative;
    width: 40px;
    height: 40px;
    /*background-color: blueviolet;
    */
    /* background-image: url('https://s24.picofile.com/file/8451585818/user.png'); */
    background-size: 60%;
    background-position: center;
    background-repeat: no-repeat;
    /*padding: 5px;
    */
    /* border: 1pt solid #0afc96; */
    border-radius: 50%;
    /*margin-top: 5px;
    */
    margin-right: 2.5px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    top: 50%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
}

.icon-Contact span {
    position: absolute;
    /* background-color: red; */
    height: 10px;
    width: fit-content;
    left: 50%;
    top: 50%;
    transform: translateY(-50%) translateX(-50%);
    -webkit-transform: translateY(-50%) translateX(-50%);
    -moz-transform: translateY(-50%) translateX(-50%);
    -ms-transform: translateY(-50%) translateX(-50%);
    -o-transform: translateY(-50%) translateX(-50%);
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.text-Contact {
    width: 75%;
    height: 60px;
    padding: 10px;
    /* background-color: darkorange; */
}

.text-Contact .title-Contact {
    width: 100%;
    /* background-color: red; */
    text-align: right;
    font-size: small;
    font-weight: 400;
}

.text-Contact .description-Contact {
    text-align: right;
    /* background-color: blue; */
}