    .Setting {
        width: 100vw;
        height: 100vh;
        background-color: aliceblue;
        background-image: url('https://s24.picofile.com/file/8451585250/thomas_lefebvre_mT1uxKaegSQ_unsplash_bde9b5fc5ab03a369e84.jpg');
        background-size: cover;
        background-position: center;
    }
    
    .header-Setting {
        position: static;
        width: 100%;
        height: 60px;
        background-color: #1a1a1a;
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-between;
        border-bottom-left-radius: 15px;
        border-bottom-right-radius: 15px;
        z-index: 1;
        color: white;
    }
    
    .bgb-Setting {
        width: 100vw;
        height: 100vh;
        background-color: #0f0f0f8c;
        /* padding-top: 1vh; */
    }
    
    .bg-Setting {
        margin-top: 0px;
        width: 100vw;
        height: 100vh;
        background-color: #0f0f0f;
        background-image: url(https://s25.picofile.com/file/8451911668/BeFunky_photo.png);
        background-size: cover;
    }
    
    .header-Setting .backPart-Setting {
        position: relative;
        width: 40px;
        height: 40px;
        /* background-color: red; */
        background-image: url(https://s24.picofile.com/file/8451952518/back_button.png);
        background-position: center;
        background-repeat: no-repeat;
        background-size: 65%;
        top: 50%;
        transform: rotate(-180deg) translateY(50%);
        -webkit-transform: rotate(-180deg) translateY(50%);
        -moz-transform: rotate(-180deg) translateY(50%);
        -ms-transform: rotate(-180deg) translateY(50%);
        -o-transform: rotate(-180deg) translateY(50%);
    }
    
    .refresh {
        /* position: static; */
        /* margin-left: 10px; */
        /* width: 90px !important; */
        /* height: 90px !important; */
        overflow: visible;
        /* border: 2pt solid; */
        height: 100%;
        width: 50px;
    }
    
    .refresh p {
        width: max-content;
        background-color: red;
        /* margin-left: 10px; */
        color: white;
    }
    
    .refresh .icon-refresh {
        position: relative;
        /* background-color: red; */
        width: 40px;
        height: 40px;
        margin: 0 auto;
        background-image: url(https://s24.picofile.com/file/8451911526/refresh.png) !important;
        background-position: center;
        background-size: 65%;
        background-repeat: no-repeat;
        top: 50%;
        transform: translateY(-50%);
        -webkit-transform: translateY(-50%);
        -moz-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
        -o-transform: translateY(-50%);
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
    }
    
    .refresh .text-refresh {
        /* width: 50px; */
        /* background-color: aqua; */
        font-size: medium;
        font-weight: 300;
        /* margin-left: 5px; */
    }
    
    .refresh .text-refresh p {
        /* background-color: red; */
    }
    
