.addDevice-Page1 {
    position: relative;
    width: 95vw;
    height: 80vw;
    max-height: 300px;
    /*top: 50%;
        */
    /*top: 50%;
        */
    top: 10px;
    left: 50%;
    transform: translate(-50%, -0%);
    -webkit-transform: translate(-50%, -0%);
    -moz-transform: translate(-50%, -0%);
    -ms-transform: translate(-50%, -0%);
    -o-transform: translate(-50%, -0%);
}

.addDevice-Page1 .contentAdd-Page1 {
    position: absolute;
    width: 98%;
    height: 98%;
    background-color: #1a1a1a;
    border: 2pt dashed #1d1d1d;
    border-radius: 15px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    color: white;
}

.contentAdd-Page1 .centerAdd-Page1 {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    /* background-color: red; */
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
}

.contentAdd-Page1 .centerAdd-Page1 .iconAdd-Page1 {
    /*position: absolute;
        */
    width: 75px;
    height: 75px;
    margin: 0 auto;
    border: solid #0afc96 1pt;
    /*background-color: red;
        */
    /*left: 50%;
        */
    /*top: 50%;
        */
    border-radius: 50%;
    background-image: url(https://s25.picofile.com/file/8451951926/add_1_.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}

.contentAdd-Page1 .centerAdd-Page1 .textAdd-Page1 {
    margin-top: 20px;
    text-align: center;
}

.swiper-button-next {
    transform: scale(0.75);
    -webkit-transform: scale(0.75);
    -moz-transform: scale(0.75);
    -ms-transform: scale(0.75);
    -o-transform: scale(0.75);
    /* background-image: url(); */
    margin-right: -15px;
    /* background-image: url('./public/icons/green/add\(2\).png'); */
    visibility: hidden;
}

.swiper-button-prev {
    /*visibility: hidden;
    */
    /*background-color: #0afc96 !important;
    */
    transform: scale(0.75);
    -webkit-transform: scale(0.75);
    -moz-transform: scale(0.75);
    -ms-transform: scale(0.75);
    -o-transform: scale(0.75);
    /* background-image: url(); */
    margin-left: -15px;
    visibility: hidden;
}

.swiper-pagination-bullet-active {
    background-color: #0afc96 !important;
    transform: scale(1.1) !important;
    -webkit-transform: scale(1.1) !important;
    -moz-transform: scale(1.1) !important;
    -ms-transform: scale(1.1) !important;
    -o-transform: scale(1.1) !important;
}