.Contacts {
    height: 100vh;
    width: 100vw;
    background-image: url('https://s25.picofile.com/file/8451585292/tina_dawson_Kim9COAIEGc_unsplash_5922e7ed1d3da93bbae6.jpg');
    background-position: center;
    background-size: cover;
    overflow: hidden;
    /* display: flex; */
}

.blure {
    position: fixed;
    width: 100vw;
    height: 100%;
    background-color: #009b8c4d;
    top: 0px;
    z-index: -1;
}

.addIcon-Contacts {
    position: absolute;
    width: 60px;
    height: 60px;
    background-color: #009b8cd8;
    border-radius: 50%;
    bottom: 10px;
    right: 10px;
    background-image: url('https://s24.picofile.com/file/8451585050/add_user_a3f9f4bc09c16e79a6b8.png');
    background-size: 55%;
    background-position: center;
    background-repeat: no-repeat;
    border: 3pt solid #d7ecffa2;
}

#part1-Contacts {
    position: static;
    /* background-color: aqua; */
    height: 85vh;
    overflow: scroll;
    position: relative;
    /* margin-top: 70px; */
}